import React, {useEffect} from 'react';
import * as d3 from "d3";
import {graph_nbd} from './nbd'


const Histogram = (props) => {
    useEffect(()=>{
        draw(props)
    }, [props.data ? props.data.length : 0])
    return <div className="histogram"/>
}


const draw = (props) => {
    if (!props.data) return;
    let data = props.data;
    let r = props.r;
    let alpha = props.alpha;
    let t = props.t;
    let word_count = props.word_count;

    
    d3.select('.histogram > *').remove(); // remove previous hist

    // let x_range = 20;
    let num_bins = 10;

    // set the dimensions and margins of the graph
    var margin = {top: 10, right: 30, bottom: 30, left: 40},
    width = 460 - margin.left - margin.right,
    height = 400 - margin.top - margin.bottom;

    // append the svg object to the body of the page
    var svg = d3.select(".histogram")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform",
            "translate(" + margin.left + "," + margin.top + ")");

    // X axis: scale and draw:
    var x = d3.scaleLinear()
        .domain([1, 10])     // can use this instead of 1000 to have the max of data: d3.max(data, function(d) { return +d.price })
        .range([0, width]);
        
    svg.append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(x));

    // set the parameters for the histogram
    var histogram = d3.histogram()
        .value(function(d) { return d; })   // I need to give the vector of value
        .domain(x.domain())  // then the domain of the graphic
        .thresholds(x.ticks(num_bins)); // then the numbers of bins

    // And apply this function to data to get the bins
    var bins = histogram(data);

    // Y axis: scale and draw:
    var y = d3.scaleLinear()
        .range([height, 0]);
    y.domain([0, d3.max(bins, function(d) { return d.length; })]);   // d3.hist has to be called before the Y axis obviously
    svg.append("g")
        .call(d3.axisLeft(y));

    // append the bar rectangles to the svg element
    svg.selectAll("rect")
        .data(bins)
        .enter()
        .append("rect")
            .attr("x", 1)
            .attr("transform", function(d) { return "translate(" + x(d.x0) + "," + y(d.length) + ")"; })
            .attr("width", function(d) { return x(d.x1) - x(d.x0) - 1 ; })
            .attr("height", function(d) { return height - y(d.length); })
            .style("fill", "#69b3a2")

    // graph the function
    var continuous = graph_nbd(r, alpha, t, word_count);

    svg.append("svg:path")
        .attr("d", function(d) { return d3.line() (
        x.ticks(100).map(function(xi) {
            return [ x(xi+0.5), y(continuous(xi)) ]
        }))})
        .style("stroke", "red")    // set the line colour
        .style("fill", "none");    // set the fill colour 
}

  export default Histogram;